import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';

export class StickerGrid extends PureComponent {

    render() {
        return (
            this.props.mode === 'small' ?
                <div className={'sticker-grid-small'}>{this.props.children}</div>
                :
                this.props.mode === 'big' &&
                <div className={'sticker-grid-big'}>{this.props.children}</div>
        )
    }

}